import { observer } from 'mobx-react';
import * as React from 'react';

import { Bottle, IBottle } from '../stores/BottleStore';
import { IRootStore } from '../stores/RootStore';

import { Input } from './Input';
import { Modal } from './Modal';
import Scanner from './Scanner';

interface IAddBottleProps {
    store: IRootStore;
}
interface IAddBottleState {
    bottle: IBottle;
    scanning: boolean;
    results: Array<any>;
}

@observer
export class AddBottle extends React.Component<IAddBottleProps, IAddBottleState> {
    private modal: Modal;
    private wrappedInstance: AddBottle;

    constructor(props: IAddBottleProps) {
        super(props);

        this.state = {
            bottle: { id: undefined, distillery: '', age: undefined, description: '', name: '' },
            scanning: false,
            results: []
        };

        this.handleOnClick = this.handleOnClick.bind(this);
        this.handleOnChange = this.handleOnChange.bind(this);
        this.scan = this.scan.bind(this);
        this.onDetected = this.onDetected.bind(this);
        this.open = this.open.bind(this);
    }

    private handleOnChange = (property, event) => {
        const { bottle } = this.state;
        bottle[property] = event.target.value;

        this.setState({ bottle });
    };

    private handleOnClick = () => {
        const { bottleStore, eventStore, scotchNightStore } = this.props.store;
        const { bottle } = this.state;
        const { currentEvent } = scotchNightStore;

        bottleStore.addBottle(bottle).then((newBottle: IBottle) => {
            eventStore.addBottle(currentEvent, newBottle);
            this.modal.close();
        });
    };

    private scan() {
        this.setState({ scanning: !this.state.scanning });
    }

    private onDetected(result) {
        this.setState({ results: this.state.results.concat([result]) });
    }

    public open() {
        this.modal.open();
    }

    public render() {
        const { bottle } = this.state;

        return (
            <Modal
                className="add-bottle"
                ref={modal => {
                    this.modal = modal;
                }}
            >
                <h2 className="margin-bottom-md">Add a New Bottle</h2>
                <Input type="text" value={bottle.distillery} placeholder="Distillery" onChange={this.handleOnChange.bind(this, 'distillery')} />
                <Input type="text" value={bottle.name} placeholder="Name" onChange={this.handleOnChange.bind(this, 'name')} />
                <Input type="text" value={bottle.age ? bottle.age : ''} placeholder="Age" onChange={this.handleOnChange.bind(this, 'age')} />
                <Input type="text" placeholder="Description" value={bottle.description} onChange={this.handleOnChange.bind(this, 'description')} />
                <button className="btn btn--primary btn--block" onClick={this.handleOnClick}>
                    Add Bottle
                </button>
                <button onClick={this.scan}>{this.state.scanning ? 'Stop' : 'Start'}</button>
                {this.state.scanning ? <Scanner onDetected={this.onDetected} /> : null}
            </Modal>
        );
    }
}

export default AddBottle;
